*{
    margin: 0px;
    padding: 0px;
}
.backgroundDiv{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #1d8ba1; */
    background: linear-gradient(#1d8ba1,#353838);
    /* background-image:url('bgimage.jpg'); */
}
.profileDiv{
    width: 30%;
    height: 75vh;
    background-color: #353838;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-right: 2px #1d8ba1 solid;
    /* margin-right: 2px; */

    /* display: flex; */
    /* justify-content: center; */
    /* align-items: center; */
    text-align: center;
}
.page{
    width: 55%;
    height: 75vh;
    background-color: #131414;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    text-align: center;
    /* overflow-y: auto; */
}
.page #avatar{
    display: none;
}
.page .socialMediaIcons{
    display: none !important;
}
.page .button{
    display: none;
}
.page .allrightreserve{
    display: none;
}

.menuDiv{
    width: 7%;
    height: 75vh;
    border-radius: 20px;
    background-color: #353838;
    margin-left: 30px;
    text-align: center;
}

.grow { 
    transition: all .2s ease-in-out; 
}
.grow:hover { 
    transform: scale(1.1); 
}
.shrink { 
    transition: all .2s ease-in-out; 
    }
    
.shrink:hover { 
    transform: scale(0.9); 
    }
.buttonHover:hover{
    color: #1d8ba1;
    
}

.occupationText:before{
    content: '';
    animation: animate infinite 8s;
}
@keyframes animate{
    0%{
        content: 'Web Developer';
    }
    40%{
        content: 'React Developer';
    }
    80%{
        content: 'Front-end Developer';
    }
    100%{
        content: 'Designer';
    }

}
.aboutDetails{
    height: 77%;
    width: 98%;
    /* background-color: tomato; */
    /* overflow-y: scroll; */
    margin-top: 10px;
}
.personal{
    height: 50%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: red; */
}
.description{
    width: 57%;
    height: 100%;
    /* background-color: violet; */
}
.personalInfo{
    width: 36%;
    height: 100%;
    /* background-color: yellow; */
    padding-left: 30px;
}
.whatIDo{
    width: 98%;
    height: 40%;
    margin-top: 25px;
    /* background-color: teal; */
    
}
.portfolioMainDiv{
    width: 100%;
    height:80%;
    
    /* background-color: royalblue; */
    overflow-y: auto;
}
.row{
    /* display: flex;
    justify-content: center;
    align-items: center; */
    /* background-color: red; */
    height: 100%;
    width: 100%;
}

.projectImage{
    width: 98%;
    height: 98%;
    border-bottom: 2px #1d8ba1 solid;
    /* background-color: saddlebrown; */
}
.hamburgerMenu{
    display: none;
}


.shrink {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black;
  }
  
  .shrink .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
  
    /* Position the tooltip */
    position: absolute;
    z-index: 1;
  }
  
  .shrink:hover .tooltiptext {
    visibility: visible;
  }

::-webkit-scrollbar{
    width:10px;
}
::-webkit-scrollbar-thumb{
    border-radius: 20px;
    /* background-color:  #1d8ba1; */
    background: linear-gradient(#1d8ba1,#353838);
}

.cardButton{
margin-right: 50px !important;
}


.contactFormMain{
    width: 100%;
    height: 80%;
    /* background-color: blue; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.form{
    width: 70%;
    height: 100%;
    /* background-color: yellow; */
}
.formInfo{
    width: 25%;
