    align-items: center;
}
.contactFormInputField .row1{
    /* background-color: red; */
    height: 20%;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.contactFormInputField .row2{
    /* background-color: blue; */
    height: 20%;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.contactFormInputField .row3{
    /* background-color: purple; */
    height: 20%;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.contactFormInputField .row3 textarea{
    width: 100% !important;
    height: 60% !important;
}
.contactFormInputField .row4{
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: grey; */
    height: 20%;
